    text-shadow: none;
}

#how_we_work .thumb{
    position: relative;
    transition: all 0.5s;
}

#how_we_work .thumb:hover{
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

#how_we_work .thumb:hover:before{
    opacity: 1;
    visibility: visible;
}

#how_we_work .thumb:hover:after{
    opacity: 1;
    visibility: visible;
}

#how_we_work .thumb:hover h1{
    -webkit-text-stroke: 1px #f16638; 
}

#how_we_work .thumb:after{
    display: block;
    content: "";
    width: 60px;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    border-top: 2px solid #f16638;
    border-left: 2px solid #f16638;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s;
}

#how_we_work .thumb:before{
    display: block;
    content: "";
    width: 60px;
    height: 60px;
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom: 2px solid #f16638;
    border-right: 2px solid #f16638;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s;
}

